import React, { Component } from 'react';
import Grid from './../grid/Grid'
// import PropTypes from 'prop-types';

class Figure {
    constructor(shape, x, y, color) {
        this.shape = shape,
        this.y = y;
        this.x = x;
        this.color = color;
    }
}

class Controller extends Component {
//
//     static contextTypes = {
//         loop: PropTypes.object,
//     };
//
    constructor () {
        super();

        this.state = {
            cells: this.generateCells(20, 10),
            figures: [],
            activeFigure: undefined
        };
    }

    generateCells(h, w) {
        let cells = [];

        for (let i = 0; i < h; i++) {
            let row = [];

            for (let j = 0; j < w; j++) {
                if (/*(i === 0) ||*/ (i === h-10) || (j === 0) || (j === w-1)) { // setting borders for board;
                    row.push(1);
                } else { // setting empty cells on board;
                    row.push(0);
                }
            }
            cells.push(row);
        }
        return (cells);
    }

    generateFigure() {
        return(new Figure([[0,1,0], [0,1,0], [0,1,0]], 1, 2, '#fff'));
    };

    clearScreen(c, oldFig) {
        for (let i = 0; i < 3; i++) {
            for (let j = 0; j < 3; j++) {
                if ((c[i+oldFig.y-1][j+oldFig.x-1] === 1) && (oldFig.shape[i][j] === 1))  {
                    c[i+oldFig.y-1][j+oldFig.x-1] = 0;
                }
            }
        }
        return(c);
    };

    updateBoard (c, newFig) {
        if ((newFig.x - 1 >= 0) && (newFig.x + 1 < 10)) {
            if ((newFig.y - 1 >= 0) && (newFig.y + 1 < 20)) {
                // shape inside board, we can update board
                for (let i = 0; i < 3; i++) {
                    for (let j = 0; j < 3; j++) {
                        if (c[i+newFig.y-1][j+newFig.x-1] !== 1) {
                            c[i+newFig.y-1][j+newFig.x-1] = newFig.shape[i][j];
                        }
                    }
                }
            }
        }
        return(c);
    };

    moveLeft = () => {
        this.update(4);
    };

    moveRight = () => {
        this.update(6);
    };

    update = (t) => {
        let tempFig = this.state.activeFigure;
        let state = this.state;
        let tempCells = state.cells;

        // console.log(t);

        if (tempFig !== undefined) {
            // we have active figure
            // make a copy of current board
            let boardCpy = tempCells.map(function(arr) {
                return arr.slice();
            });

            tempCells = this.clearScreen(tempCells, tempFig);

            switch (t) {
                case 4:
                    tempFig.x -= 1;
                    break;
                case 6:
                    tempFig.x += 1;
                    break;
                default:
                    tempFig.y += 1;
            }
                if (!this.collides(tempCells, tempFig)) {
                    state.activeFigure = tempFig;
                    state.cells = this.updateBoard(state.cells, state.activeFigure);
                    this.setState(state);
                } else {
                    // todo: check for defautl (down) move first

                    switch (t) {
                        case 4:
                            tempFig.x += 1;
                            break;
                        case 6:
                            tempFig.x -= 1;
                            break;
                    }
                    tempFig.y += 1;

                    if (!this.collides(tempCells, tempFig)) {
                        state.activeFigure = tempFig;
                        state.cells = this.updateBoard(state.cells, state.activeFigure);
                        this.setState(state);
                    } else {
                        state.activeFigure = undefined;
                        state.cells = boardCpy.map(function(arr) {
                            return arr.slice();
                        });
                        this.setState(state);
                    }
                }
        } else {
            // we have to create active figure
            let temp = this.generateFigure();

            if (this.collides(tempCells, temp)) {
                // game over
                // alert("game over");
                console.log("game over");

            } else {
                // temp is our new active figure
                state.activeFigure = temp;
                state.cells = this.updateBoard(state.cells, state.activeFigure);
                this.setState(state);
            }
        }

        console.log("tick");

        // tick logic
    };

    collides(cells, figure) {
        // function returns true if atleast one of figure cells overlies not empty cell from cells.
        for (let i = 0; i < 3; i++) {
            for (let j = 0; j < 3; j++) {

                // console.log("i:", i, "j:", j, "in:", i+figure.y-1, "jn:", j+figure.x-1 );
                // console.log("c", cells[i+figure.y-1][j+figure.x-1], figure.shape[i][j] );
                // if (i+figure.y-1 >= 19) {
                //     return(true);
                // } else {
                    if ((figure.shape[i][j] === 1) && (cells[i+figure.y-1][j+figure.x-1] === figure.shape[i][j])) {
                        // abort draw
                        // console.log("abort draw");
                        return(true);
                    }
                // }
            }
        }
        // console.log("afford draw");
        return(false);
    };

    render() {
        return (
            <div className="gridWrapper">
                <Grid cells={this.state.cells} />

                <div className="controls">
                    <button className="controls controls--tick" onClick={this.update}>tick</button>

                    <button className="controls controls--left" onClick={this.moveLeft}></button>
                    <button className="controls controls--right" onClick={this.moveRight}></button>
                </div>
            </div>
        );
    };
}

/* <button className="controls controls--up" onClick={this.update}></button>
<button className="controls controls--down" onClick={this.update}></button> */

export default Controller;



