import React, { Component } from 'react';

import './Grid.css';

import { Sprite } from 'react-game-kit';


import imageThatIsMine from './assets/a.png';



// import { Loop } from 'react-game-kit';

class Grid extends Component {

    constructor (props) {
        super(props);

        // this.state = {
        //     cells: props.cells
        // };
    }

    prepareRow (r) {
        return(
            r.map((c, i) => {
                let color = "black";

                c === 1 ? color = "white" : color = "black";
                return(
                    <span className={"cell cell--" + color} key={i}>{c}</span>
                );
            })
        );
    };

    render() {
        // console.log(require("./assets/a.png"));

        return (
            <div className="Grid">
                <span>{
                    this.props.cells.map((r, i) => {
                        return(
                            <div className="row" key={i}>{this.prepareRow(r)}</div>
                        );
                    })}
                </span>

                <Sprite
                    repeat={true}
                    src={imageThatIsMine}
                    state={0}
                    steps={[9, 9, 0, 4, 5]}
                />



            </div>



        );
    };
}





export default Grid;



